.onoffswitch {
  position: relative;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
}

.onoffswitch-checkbox {
  display: none;
}

.onoffswitch-label {
  display: block;
  overflow: hidden;
  cursor: pointer;
  /*border: 2px solid #999999;*/
  border-radius: 0.25rem;
  margin: 0px;
}

.onoffswitch-inner {
  display: block;
  width: 200%;
  margin-left: -100%;
  transition: margin 0.2s ease-in 0s;
}

.onoffswitch-inner:before,
.onoffswitch-inner:after {
  display: block;
  float: left;
  width: 50%;
  height: inherit;
  padding: 0;
  height: 38px;
  line-height: 38px;
  /*font-size: 14px;*/
  color: white;
  font-family: Trebuchet, Arial, sans-serif;
  box-sizing: border-box;
}

.onoffswitch-inner:before {
  content: "Publish";
  background-color: #29be5b;
  color: #ffffff;
}

.onoffswitch-inner:after {
  content: "Publish";
  /*padding-right: 10px;*/
  background-color: #eeeeee;
  color: #999999;
  /*text-align: right;*/
}

.onoffswitch-switch {
  display: block;
  width: 20px;
  /*margin: 6px;*/
  background: #999999;
  position: absolute;
  top: 0;
  bottom: 0;
  right: calc(100% - 20px);
  border-radius: 0.25rem;
  transition: all 0.2s ease-in 0s;
}

.onoffswitch-checkbox:checked + .onoffswitch-label .onoffswitch-inner {
  margin-left: 0;
}

.onoffswitch-checkbox:checked + .onoffswitch-label .onoffswitch-switch {
  right: 0px;
  background: #00aa00;
}

